<template>
    <div class="p-grid">
        <div class="p-col-12 p-md-6 p-lg-4 p-md-offset-3 p-lg-offset-4 p-mt-5">
            <p-card>
                <template #title> Login </template>
                <template #content>
                    <div class="p-fluid">
                        <div class="p-field">
                            <label for="firstname">Username</label>
                            <p-input-text
                                type="text"
                                v-model="username"
                                class="p-inputtext-lg"
                                :class="{ 'p-invalid': invalid }"
                            />
                        </div>
                        <div class="p-field">
                            <label for="lastname">Password</label>
                            <p-password
                                v-model="password"
                                toggleMask
                                :feedback="false"
                                class="p-inputtext-lg"
                                :class="{ 'p-invalid': invalid }"
                            />
                        </div>
                        <p-message severity="error" v-if="invalid">
                            Username or Password incorrect
                        </p-message>
                    </div>
                </template>
                <template #footer>
                    <p-button
                        icon="pi pi-sign-in"
                        label="Login"
                        :loading="isLoading"
                        @click="login()"
                    />
                </template>
            </p-card>
        </div>
    </div>
</template>

<script>
import { onMounted, ref } from "vue";
import { useStore } from "vuex";
import router from "@/router";
import { useRoute } from "vue-router";

export default {
    setup() {
        const store = useStore();

        const username = ref();
        const password = ref();
        const isLoading = ref(false);
        const invalid = ref(false);

        const route = useRoute();

        const login = () => {
            store
                .dispatch("login", {
                    username: username.value,
                    password: password.value
                })
                .then(
                    () => {
                        if (route.query.patientId && route.query.to) {
                            const { patientId, to } = route.query;
                            router.push({
                                name: "redirect",
                                query: { patientId, to }
                            });
                        } else {
                            router.push({ name: "ArtCycle" });
                        }
                    },
                    (error) => {
                        invalid.value = true;
                    }
                );
        };

        onMounted(() => {
            if (store.state.user != null) {
                router.push({ name: "ArtCycle" });
            }
        });

        return {
            username,
            password,
            isLoading,
            invalid,
            login
        };
    }
};
</script>
